@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
/* ------- Form -------- */

/* ------- Logo -------- */
.flex_logo {}

.logo {
  width: 400px;
}
/* --------------------- */

/* ------- Put the content in the middle -------- */

.flex_custom {
  padding: 0;
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
/* ----------------------------------------------- */

.game_form {
  width: 100%;
  position: relative;
}

.input_game {
  width: 50%;
  font-size: 50px;
  border: 5px solid #121212;
  border-radius: 4px;
}

.input_game:focus {
  border: 5px solid #dc0c00;
  outline: none;
  border-radius: 3px;
}

.label_custom {
  font-size: 20px;
}

.label_error {
  color: #dc0c00;
}

.btn_start_game {
  background-color: #dc0c00;
  color: #ffffff;
  padding: 20px 50px;
  border: none;
  font-size: 30px;
  margin-top: 20px;
}

.btn_start_game:hover {
  text-decoration: none;
  color: #121212;
  text-decoration: none;
  background-color: #ff4a4a;
}

.btn_start_game:focus {
  outline: none;
  box-shadow: none;
}
.btn_disable {
  background-color: #b3b3b3;
  pointer-events: none;

}

/* ---------- Loading ----------- */

.bottle {
  -webkit-animation: loading 1.4s infinite linear;
          animation: loading 1.4s infinite linear;
  width: 100px;
}

.title h4 {
  margin-top: 20px;
}

@-webkit-keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* -------------------------------- */

/* --------------- Hangman ----------------- */

pre {
  white-space: pre-wrap;
  margin: 0 auto;
}

.game_word {
  font-size: 4rem;
  font-family: 'Bebas Neue', 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
}

.liver {
  width: 320px;
}

